import { useNavigate } from 'react-router-dom';
import ReactMarkdown from 'react-markdown'
import rehypeRaw from 'rehype-raw'
import parseDate from "../../function/parseDate";
import './article.css'
import {
    WhatsappShareButton,
    FacebookShareButton,
    TwitterShareButton,
    TelegramShareButton,
    RedditShareButton,
    FacebookIcon,
    WhatsappIcon,
    TwitterIcon,
    TelegramIcon,
    RedditIcon,
} from "react-share";

export default function Article(props) {
    const navigate = useNavigate()
    function handleClick(tag){
      if(!props.disableLink){
        navigate(`/tag/${tag}`)
        setTimeout(()=>{document.documentElement.scrollTop = 0},200)
      }
    }
    return(
        <> 
            <img className="articolo-image" alt="" src={props.imageURL}/>
            <div className="articolo--info">
                <p className="articolo--date" >{parseDate(props.date)} |</p> 
                <p className="articolo--tag" onClick={()=> handleClick(props.tag[0])}>{props.tag[0]}</p>
            </div>
            <div className="articolo--head">
                <h1 className="articolo-title">{props.title}</h1>
                <h2 className="articolo-subtitle">{props.subtitle}</h2>
                <div className="articolo--share">
                    <FacebookShareButton url={`https://www.sindacodelcalciomercato.it/${props.titleUrl}`}>
                        <FacebookIcon size={35} borderRadius={10}/>
                    </FacebookShareButton>
                    <WhatsappShareButton url={`https://www.sindacodelcalciomercato.it/${props.titleUrl}`}>
                        <WhatsappIcon size={35} borderRadius={10}/>
                    </WhatsappShareButton>
                    <TwitterShareButton url={`https://www.sindacodelcalciomercato.it/${props.titleUrl}`}>
                        <TwitterIcon size={35} borderRadius={10}/>
                    </TwitterShareButton>
                    <TelegramShareButton url={`https://www.sindacodelcalciomercato.it/${props.titleUrl}`}>
                        <TelegramIcon size={35} borderRadius={10}/>
                    </TelegramShareButton>
                    <RedditShareButton url={`https://www.sindacodelcalciomercato.it/${props.titleUrl}`}>
                        <RedditIcon size={35} borderRadius={10}/>
                    </RedditShareButton>
                </div>
            </div>
            <ReactMarkdown rehypePlugins={[rehypeRaw]} className="articolo-body" children={props.text}/>
            <div className="articolo--share--bottom">
                    <FacebookShareButton url={`https://www.sindacodelcalciomercato.it/${props.titleUrl}`}>
                        <FacebookIcon size={35} borderRadius={10}/>
                    </FacebookShareButton>
                    <WhatsappShareButton url={`https://www.sindacodelcalciomercato.it/${props.titleUrl}`}>
                        <WhatsappIcon size={35} borderRadius={10}/>
                    </WhatsappShareButton>
                    <TwitterShareButton url={`https://www.sindacodelcalciomercato.it/${props.titleUrl}`}>
                        <TwitterIcon size={35} borderRadius={10}/>
                    </TwitterShareButton>
                    <TelegramShareButton url={`https://www.sindacodelcalciomercato.it/${props.titleUrl}`}>
                        <TelegramIcon size={35} borderRadius={10}/>
                    </TelegramShareButton>
                    <RedditShareButton url={`https://www.sindacodelcalciomercato.it/${props.titleUrl}`}>
                        <RedditIcon size={35} borderRadius={10}/>
                    </RedditShareButton>
                </div>
            <div className='articolo--tagContainer'>
            <p className='articolo--tagTitle'>Tag:</p>
            {props.tag.map((t,key)=>{
                if(key !== (props.tag.length -1)){
                    return <p className='articolo--allTags' key={key} onClick={()=> handleClick(t)} >{t},</p>
                }
                return <p className='articolo--allTags' key={key} onClick={()=> handleClick(t)} >{t}</p>
            })
            }
            </div>
        </>
    )
}
